#filter-menu-container {
  display: flex;
  flex-flow: row nowrap;
  place-items: center center;
  place-content: center center;
  
  width: 100%;
  height: 100%;

  box-sizing: border-box;
  padding: 12px;
  gap: 12px;

  font: normal 400 16px "Outfit", sans-serif;
  
  /* Filter Categories */
  > * {
    display: flex;
    flex-flow: column nowrap;
    place-items: center center;
    place-content: center center;
      
    width: 180px;
    height: 100%;

    box-sizing: border-box;
    outline: 2px solid rgba(255, 255, 255, 1.00);

    /* Label */
    > :nth-child(1) {
      display: flex;
      flex-flow: row nowrap;
      place-items: start center;
      place-content: center start;

      flex: 1 1 auto;

      width: 100%;
      height: 100%;

      box-sizing: border-box;
      padding: 8px;

      font: normal 400 16px "Outfit", sans-serif;
      color: rgba(255, 255, 255, 1.00);
    }

    /* Interactive Components */
    > :nth-child(2) {
      flex: auto 1 auto;
    
      width: 100%;
      height: auto;

      box-sizing: border-box;

      cursor: pointer;
    }
      
    /* Input Field */
    > * > input {
      width: 100%;
      height: 36px;

      box-sizing: border-box;
      padding-block: 0;
      padding-inline: 8px;

      font: normal 400 16px "Outfit", sans-serif;
      text-transform: capitalize;
      color: rgba(100, 100, 100, 1.00);
    }

    /* Dropdown Fields */
    > * > :not(input) {
      overflow: clip clip;
      white-space: nowrap;

      width: 100%;
      max-width: 100%;
      height: auto;
      max-height: 40px;

      box-sizing: border-box;
      padding: 8px;

      background-color: rgba(255, 255, 255, 1.00);

      font: normal 400 16px "Outfit", sans-serif;
      text-transform: capitalize;
      text-overflow: ellipsis;
      color: rgba(100, 100, 100, 1.00);
    }

    /* Inactive Dropdown Lists */
    > .filter-menu-dropdown-inactive > :nth-child(2) {
      display: none;
    }

    /* Dropdown Lists */
    > .filter-menu-dropdown-active > :nth-child(2) {
      position: absolute;
      z-index: 100;

      display: block;

      overflow: hidden auto;
  
      width: 180px;
      max-width: 180px;
      height: auto;
      max-height: 360px;

      box-sizing: border-box;
      outline: 2px solid rgba(255, 255, 255, 1.00);
      padding: 0;
      
      background-color: rgba(255, 255, 255, 1.00);

      /* Dropdown Item */
      > * {
        overflow: clip clip;
        white-space: wrap;

        width: 100%;
        height: auto;

        box-sizing: border-box;
        border: 8px solid rgba(0, 0, 0, 0.00);

        font: normal 400 16px "Outfit", sans-serif;
        text-transform: capitalize;
        color: rgba(100, 100, 100, 1.00);

        &.filter-menu-item-selected {
          background-color: rgba(225, 125, 25, 1.00);

          color: rgba(255, 255, 255, 1.00)
        }

        &:hover {
          background-color: rgba(225, 125, 25, 1.00);

          color: rgba(255, 255, 255, 1.00)
        }
      }
    }
  }
}

